	module.exports = {"data":{"site":{"siteMetadata":{"title":"Dev Blog","author":"Ryan Fitzgerald"}},"markdownRemark":{"id":"D:/GitHub/devblog/src/pages/2015-05-01-hello-world/index.md absPath of file >>> MarkdownRemark","html":"<p>This is my first post on my new fake blog! How exciting!</p>\n<p>I’m sure I’ll write a lot more interesting things in the future.</p>\n<p>Oh, and here’s a great quote from this Wikipedia on <a href=\"http://en.wikipedia.org/wiki/Salted_duck_egg\">salted duck eggs</a>.</p>\n<blockquote>\n<p>A salted duck egg is a Chinese preserved food product made by soaking duck eggs in brine, or packing each egg in damp, salted charcoal. In Asian supermarkets, these eggs are sometimes sold covered in a thick layer of salted charcoal paste. The eggs may also be sold with the salted paste removed, wrapped in plastic, and vacuum packed. From the salt curing process, the salted duck eggs have a briny aroma, a gelatin-like egg white and a firm-textured, round yolk that is bright orange-red in color.</p>\n</blockquote>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; margin: 15px -30px !important; max-width: 700px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Chinese Salty Egg\"\n        title=\"\"\n        src=\"/devblog/static/salty_egg-8058f3f26913fea3b6a89a73344fe94a-177e4.jpg\"\n        srcset=\"/devblog/static/salty_egg-8058f3f26913fea3b6a89a73344fe94a-6c2d5.jpg 175w,\n/devblog/static/salty_egg-8058f3f26913fea3b6a89a73344fe94a-3d17c.jpg 350w,\n/devblog/static/salty_egg-8058f3f26913fea3b6a89a73344fe94a-177e4.jpg 700w,\n/devblog/static/salty_egg-8058f3f26913fea3b6a89a73344fe94a-40bb7.jpg 1050w,\n/devblog/static/salty_egg-8058f3f26913fea3b6a89a73344fe94a-562d0.jpg 1400w,\n/devblog/static/salty_egg-8058f3f26913fea3b6a89a73344fe94a-24ad8.jpg 2048w\"\n        sizes=\"(max-width: 700px) 100vw, 700px\"\n      />\n    </span>\n  </span>\n  </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"token function\">$</span><span class=\"token punctuation\">(</span>document<span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">ready</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">function</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    console<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span><span class=\"token string\">'here'</span><span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span></code></pre>\n      </div>","frontmatter":{"title":"Hello World","date":"May 01, 2015","featuredImage":null}}},"pathContext":{"slug":"/2015-05-01-hello-world/","previous":null,"next":{"fields":{"slug":"/2015-05-06-my-second-post/"},"excerpt":"Wow! I love blogging so much already. Did you know that “despite its name, salted duck eggs can also be made from chicken eggs, though the…","frontmatter":{"title":"My Second Post!","date":"May 6, 2015","featuredImage":null}}}}

